.article {
    &-meta {
        padding: 10px 0;
    }

    &-category {
        font-family: $font-roboto;
        font-weight: $weight-bold;
        letter-spacing: 1px;
        color: $color-primary;
        font-size: 12px;
        line-height: 14px;
        text-transform: uppercase;
        padding: 0;
        margin: 0;
    }

    &-title {
        margin: 11px 0 10px;
        font-family: $font-roboto;
        font-size: 28px;
        -webkit-font-smoothing: antialiased;
        font-weight: $weight-light;
        line-height: 32px;
    }
}